@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&family=Roboto:wght@400;700&display=swap);
*,
html,
body {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Montserrat, Roboto, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.container header {
  height: 20vh; /* <<<<<<<<<< complementary height */
  color: #777;
  background: #f7f7f7;
  margin-top: 0;

  display: flex;
  align-items: center;
  justify-content: center;
}

.container header h1 {
  font-family: Montserrat, Roboto, sans-serif;
  font-weight: 400;
}

.container main {
  text-align: center;
  max-width: 90vw;
  margin: 0 auto;
  /*min-height: 80vh;  <<<<<<<<<< complementary height*/
  margin-top: 1rem;
}

.info-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  border-radius: 14px;
  /* background: #ddd; */
  margin-bottom: 24px;
  padding: 4px;
  transition: 500ms;
  background: linear-gradient(45deg, #a538ff, #17dbdf);
}

.fade-in {
  animation: fadein 1s forwards;
}

.copypaste-watermark {
  margin: 0 auto;
  padding: 15vh 0;
  opacity: 0;
  animation: fadein 1s forwards;
}

.copypaste-watermark svg {
  width: 30vw;
  max-width: 300px;
  fill: #777;
}

.copypaste-watermark p {
  font-size: 2.5rem;
  color: #555;
}

.container-loading {
  height: 100vh;
  width: 100vw;
  padding-bottom: 10vh;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  font-size: 1.25rem;
}

@keyframes progressAnimation {
  0% {
    background-position: -50px;
  }
  50% {
    background-position: 50px;
  }
  100% {
    background-position: -50px;
  }
}

.container-loading .progress {
  height: 8px;
  width: 12rem;

  position: relative;
  border-radius: 10px;
  transition: 1s;

  overflow: hidden;
  /* border: 1px solid red; */
}

.inner-progress {
  height: 40px;
  width: 18rem;

  position: absolute;
  left: 50%;
  transform: translateX(-50%);

  background: linear-gradient(45deg, #a538ff 0%, #17dbdf 100%);
  animation: progressAnimation 2s ease-in 0s infinite;

  /* border: 1px solid #333; */
}

.text-content {
  flex-grow: 1;
  background: #eee;
  padding: 16px 24px;
  border-radius: 0 10px 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.text-content pre {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  text-align: left;
}

.text-content span.badge {
  position: absolute;
  right: 8px;
  bottom: 8px;
  opacity: 0;
  animation: fadeout 1s forwards;
}

.text-content:hover span.badge {
  animation: fadein 1s forwards;
}

.btn-close {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  border: none;
  background: linear-gradient(45deg, #1dd3e0, #17dbdf);
  border-bottom-left-radius: 10px;
}

.btn-close svg {
  min-height: 2rem;
  min-width: 2rem;
  max-height: 40px;
  max-width: 40px;
}

.btn-close svg * {
  fill: #fff;
}

.image-content {
  flex-grow: 1;
  overflow: hidden;
  border-radius: 10px 0 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.image-content img {
  min-width: 100%;
  min-height: 100%;
}

.image-content span.badge {
  position: absolute;
  left: 8px;
  bottom: 8px;

  opacity: 0;
  animation: fadeout 1s forwards;
}

.image-content:hover span.badge {
  animation: fadein 1s forwards;
}

span.badge {
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
}

